<script>
import { mapState } from "vuex"
import { mapMutations } from "vuex"
import Clipboard from "clipboard";
import dappAbi from "../common/abi/dapp.json"
import cakeAbi from "../common/abi/cake.json"
export default {
  data(){
    return{
      show: false,
      quantity: '',
      networkVersion: 42,
      bnbBalance: 0,
      cakeBalance: 0,
      dappBalance: 0,
      rewards: 0,
      dappAddress:'0xCb8aA6e398Dc2031b3f173274BCC8f25FC4e764a',  //项目智能合约地址
      cakeAddress:'0xBACE1b27a22f84a6BBed1eBf5B100EDD400fb485',  //蔬菜代币合约
      loading: false,
      loading1: false,
      loading2: false,
      ceoAddress:'',
    }
  },
   watch:{
     account(){
       this.getBanlance()
     }
   },
   created(){
     if(this.account){
       setTimeout(()=>{
        this.getBanlance()
      },200) 
     }
   },
  computed:{
    i18n(){
      return this.$t("message");
    },
    ...mapState({
      account: (state) => state.account,
    }),
  },
  methods:{
      ...mapMutations(["login","loginout"]),
      addressFilter(str) {
        if (!str) return "--";
        const str1 = str.substring(0, 5);
        const str2 = str.substring(str.length - 5, str.length);
        return str1 + "..." + str2;
      },

      // 种植蔬菜
      plantFun(){
        if(!this.quantity||this.quantity==0) return this.$tools.warning(this.i18n.a20)
        if(Number(this.quantity) > Number(this.bnbBalance)) return this.$tools.warning(this.i18n.a21)
        if(this.loading) return
        this.loading = true

        let cakeContract = new web3.eth.Contract(cakeAbi,this.cakeAddress)
        // 授权
        cakeContract.methods.approve(this.dappAddress,"0x" + Number(10000000000000000000000).toString(16)).send({from:this.account}).then(res=>{
          // 种植蔬菜
          let dappContract = new web3.eth.Contract(dappAbi,this.dappAddress)
          let link = window.location.href
          let arr = link.split("=")
          switch(arr.length){
            case 1:
              var ceoAddress = this.ceoAddress
            break;
            case 2:
              if(arr[1].includes('tokenpocket')){  //tp
                var ceoAddress = this.ceoAddress
              }else{  //meatmask
                var ceoAddress = arr[1]
              }
            break;
            case 3:
              var ceoAddress = arr[2]
            break;
          }
          dappContract.methods.buyEggs(
            '0x'+(this.quantity*1e18).toString(16).replace(/\.\w+$/,''),
            ceoAddress
          ).send({from:this.account} ).then(result=>{
            this.quantity = ''
            this.loading = false
            this.$tools.success(this.i18n.a23)
            this.getBanlance()
          }).catch(err=>{
              if(err.code==4001){
                this.$tools.warning(this.i18n.tip)
              }else{
                this.$tools.warning(err.message)
              }
              this.loading = false
              console.log(err)
          })
        }).catch(err=>{
            if(err.code==4001){
              this.$tools.warning(this.i18n.tip)
            }else{
              this.$tools.warning(err.message)
            }
            this.loading = false
            console.log(err)
        })
      },

      // 重新成长
      regrowFun(){
        // if(this.rewards < 0.01) return this.$tools.warning(this.i18n.a22)
        if(this.loading1) return
        this.loading1 = true
        let dappContract = new web3.eth.Contract(dappAbi,this.dappAddress)
        dappContract.methods.hatchEggs().send({from:this.account}).then(res=>{
          this.loading1 = false
          this.$tools.success(this.i18n.a24)
          this.getBanlance()
        }).catch(err=>{
          if(err.code==4001){
            this.$tools.warning(this.i18n.tip)
          }else{
            this.$tools.warning(err.message)
          }
          this.loading1 = false
        })   
      },

      // 收成
      collectFun(){
        if(this.loading2) return
        this.loading2 = true
        let dappContract = new web3.eth.Contract(dappAbi,this.dappAddress)
        dappContract.methods.sellEggs().send({from:this.account}).then(res=>{
          this.loading2 = false
          this.$tools.success(this.i18n.a25)
          this.getBanlance()
        }).catch(err=>{
          if(err.code==4001){
            this.$tools.warning(this.i18n.tip)
          }else{
            this.$tools.warning(err.message)
          }
          this.loading2 = false
        })
      },

      getBanlance(){
        let web3 = window.web3 = new this.$Web3(window.ethereum)
        let dappContract = new web3.eth.Contract(dappAbi,this.dappAddress)
        // 合约余额
        dappContract.methods.getBalance().call().then(res=>{
          this.dappBalance = res/1e18
          //  console.log(this.dappBalance,"this.dappBalance")
        })
        // 我的蔬菜
        dappContract.methods.getMyEggs().call({from:this.account}).then(res=>{
          this.cakeBalance = res
          console.log(this.cakeBalance,"this.cakeBalance")
        })
        // 推荐人地址
        dappContract.methods.ceoAddress().call({from:this.account}).then(res=>{
          this.ceoAddress = res
          // console.log(this.ceoAddress,"this.ceoAddress")
        })

        // cake余额
        let cakeContract = new web3.eth.Contract(cakeAbi,this.cakeAddress)
        cakeContract.methods.balanceOf(this.account).call().then(res=>{
          this.bnbBalance = res/1e18
          // console.log(this.bnbBalance,"this.bnbBalance")
        })

        setTimeout(()=>{
          // 我的奖励
          dappContract.methods.calculateEggSell('0x'+(this.cakeBalance).toString(16).replace(/\.\w+$/,'')).call({from:this.account}).then(res=>{
            this.rewards = res/1e18
            // console.log(this.rewards,"this.rewards")
          })
        },1000)

      },

      // 第三方链接跳转
      linkJump(url){
        window.open(url)
      },
      
      // 检测钱包环境
      async connectWallet(){
        const { ethereum } = await window;
        if(!(ethereum && ethereum.isMetaMask)){
          this.$storage.remove("account")
          return this.$tools.warning(this.i18n.a26)
        }else if( ethereum.networkVersion != this.networkVersion ){
          this.$tools.warning(this.i18n.a27)
          // setTimeout(() => {
          //   ethereum.request({
          //       method:"wallet_addEthereumChain",
          //       params:[{
          //           chainId: new this.$Web3(window.ethereum).utils.numberToHex(Number(this.networkVersion)),
          //           chainName: this.networkVersion===97? "Kovan测试网" : "BSC主网",
          //           nativeCurrency: {
          //               name: "tbnb",
          //               symbol: "BNB",
          //               decimals: 18
          //           },
          //           rpcUrls: this.networkVersion===42?['https://data-seed-prebsc-1-s1.binance.org:8545']:['https://bsc-dataseed1.binance.org'],
          //           blockExplorerUrls: this.networkVersion===42?['https://testnet.bscscan.com']:['https://bscscan.com'],
          //       }]
          //   })
          // }, 200);
          // 1：以太坊主网 2：Morden测试链 3：Ropsten测试链 4：Rinkeby测试链 42：Kovan测试链 56：币安链主网 BNB 97：币安链测试 BNB
        }else{
          console.log("用户钱包环境正常")
          this.start()
          this.connect();
        }
      },

      // 监控账户
      start() {
        this.web3 = window.web3 = new this.$Web3(window.ethereum);
        window.ethereum.autoRefreshOnNetworkChange = false;
        // 监控切换地址
        window.ethereum.on("accountsChanged", (item) => {
          this.login(item[0])
          console.log('accountsChanged', item[0])
        });
        // 监控切换网络
        window.ethereum.on("networkChanged", () => {
          this.loginout()
          console.log('networkChanged')
        });
      },
      
      // 语言切换
      setLang(lang){
        this.$i18n.locale = lang
        this.$storage.set("language",lang)
      },

      // 链接钱包
      connect(){
        ethereum.request({
        method: 'wallet_requestPermissions',
         params: [{ eth_accounts: {} }],
      }).then( async(permissions) => {
        if( permissions[0].parentCapability=="eth_accounts" ){
          const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
          this.login(accounts[0])
          this.$tools.success(this.i18n.a28)
        }
      })
    },
  }
}
</script>>

